import React from "react";
import "./SignIn.css";

import Theme from "../../../../Theme/Theme";

class SignIn extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <div>
        <div className="PageContainer mb-4">
          <h4>Sign In</h4>
          <small className="text-muted">Hi there! Nice to see you again.</small>
        </div>

        <div className="EmailInputContainer mb-4">
          <div className="row">
            <div className="col-2"></div>
            <h6
              id="EmailHeader"
              style={{ color: this.props.loginState.alertColor }}
            >
              Email:
            </h6>
          </div>
          <div className="row">
            <div className="col-2"></div>
            <input
              className="col"
              type="text"
              placeholder="email@email.com"
              name="email"
              style={{ color: Theme.companySecondaryTextColor }}
              onChange={this.props.handleInputChange}
              id="InputEmail"
            />
            <div className="col-2"></div>
          </div>
        </div>

        <div className="PasswordInputContainer mb-4">
          <div className="row">
            <div className="col-2"></div>
            <h6 style={{ color: this.props.loginState.alertColor }}>
              Password:
            </h6>
          </div>
          <div className="row">
            <div className="col-2"></div>
            <input
              className="col"
              type="password"
              placeholder="*********"
              name="password"
              onChange={this.props.handleInputChange}
              id="InputPassword"
            />
            <span className="material-icons underlineIcon">visibility</span>
            <div className="col-2"></div>
          </div>
        </div>

        <div>
          {this.state.showError ? (
            <p style={{ color: this.props.loginState.alertColor }}>
              {this.props.loginState.invalidField}
            </p>
          ) : null}
          <div className="SignInContainer row mb-5">
            <div className="col-2"></div>
            <div
              className="btn col"
              onClick={this.props.handleSignIn}
              id="SignInButton"
              style={
                {
                  backgroundColor: Theme.companyPrimaryBGColor,
                  color: Theme.whiteText,
                } || { backgroundColor: this.props.loginState.alertColor }
              }
            >
              Sign In
            </div>
            <div className="col-2"></div>
          </div>

          <div className="ExtraButtons row pt-4">
            <div
              className="col-6 pl-4"
              role="button"
              style={{ color: Theme.companyThirdTextColor }}
            >
              Forgot Password?
            </div>
            <div
              className="col-6"
              role="button"
              onClick={this.props.changeToSignUp}
              id="ChangeToSignUpButton"
              style={{ color: Theme.companyGreenTextColor }}
            >
              Sign Up
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default SignIn;
