import React from "react";

import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";

import TimePicker from "react-time-picker";
import "react-time-picker/dist/TimePicker.css";

import Cloud from "../../resources/cloud1.svg";

import "./Schedule.css";

import Theme from '../../../Theme/Theme'

class Schedule extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      dateForDisplay: "",
      time: "10:00",
    };
  }

  onChangeCalendar = (date) =>
    this.setState({
      date: date,
      dateForDisplay: Intl.DateTimeFormat("en-US", {
        year: "numeric",
        month: "short",
        day: "2-digit",
      }).format(date),
    });

  onChangeTime = (time) => this.setState({ time });

  render() {
    return (
      <div className="container text-center">
        <div className="BrandingContainer">
          <img className="CloudIconResize" src={Theme.companyLoginImage} alt=" filler" />
        </div>
        <h4 style={{color: Theme.companySecondaryTextColor}}>
          <b>Your Calendar</b>
        </h4>
        <div className="">
          <small className="text-muted"> showing earliest available</small>
        </div>

        <div className="text-center">
          <div className="row mt-4 mb-4">
            <div className="col"></div>
            <Calendar
              onChange={this.onChangeCalendar}
              value={this.state.date}
              className="col-3 col-xs-12 overwriteCalendarCss"
            />
            <div className="col"></div>
          </div>

          <div>
            <TimePicker onChange={this.onChange} value={this.state.time} />
          </div>

          <div
            className="btn mt-3"
            onClick={() => console.log("schedule appointment fired")}
            style={{ backgroundColor: Theme.companyPrimaryBGColor,
              color: Theme.whiteText
              }}
          >
            schedule appointment
          </div>

          <div className="mt-2 mb-2">
            <small className="text-muted">
              Patient will be notified of time and date.
            </small>
          </div>
        </div>

        <div
          className="btn"
          onClick={() =>
            this.props.togglePatientDetails(this.props.currentPatient, true)
          }
          style={{ backgroundColor: Theme.companySecondaryBGColor,
          color: Theme.whiteText
          }}
        >
          back to patient details
        </div>
      </div>
    );
  }
}

export default Schedule;
