import React from "react";
import DeviceSync from "./DeviceSync/DeviceSync";
import ShowOptions from "./Options/ShowOptions";
import ReadingDetails from "./Readings/ReadingDetails";

class WelcomePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      user: "",
      action: "choosing",
      device: "Blood Pressure",
      syncing: false,
    };
  }

  handleAction = (action, device) => {
    console.log(this.state);

    this.setState({
      action,
      device,
    });
    console.log(this.state);
  };

  renderComponent() {
    switch (this.state.action) {
      case "syncing":
        return (
          <DeviceSync
            device={this.state.device}
            handleAction={this.handleAction}
          />
        );
      case "loaded":
        // console.log("loaded device readings")
        return <ReadingDetails device={this.state.device} />;
      default:
        return <ShowOptions handleAction={this.handleAction} />;
    }
  }

  showReadings = () => {
    this.setState({
      action: "loaded",
    });
  };

  showOptionsButtons = () => {
    this.setState({
      action: "choosing",
    });
  };

  render() {
    return <div>{this.renderComponent()}</div>;
  }
}

export default WelcomePage;
