import React, { useEffect, useState } from "react";
import {
  // highBpmReadings,
  // lowBpmReadings,
  highGlucoseReadings,
  lowGlucoseReadings,
} from "./mockData";
import Theme from '../../../../Theme/Theme'


function ReadingDetails(props) {
  // const [currentTime, changeCurrentTime] = useState(new Date());
  const [dataCaptured, changeView] = useState({});
  const [showAlert, triggerAlert] = useState(false);
  const [readingLimit, changeThreshold] = useState(null);

  const bpmUpperThreshold = {
    Systolic: 180,
    Diastolic: 120,
    "Heart Rate": 180,
  };
  const glucoseUpperThreshold = { "Blood Sugar Level": 180 };
  // const glucoseLowerThreshold = { "Blood Sugar Level": 70 }

  useEffect(() => {
    let device = props.device;
    let randomIndex;
    randomIndex = Math.floor(Math.random() * 3);

    if (device === "Blood Pressure") {
      if (Math.random() > 0.5) {
        triggerAlert(true)
        // changeView(highBpmReadings[randomIndex]);
        changeView({ Systolic: 180, Diastolic: 95, "Heart Rate": 80 })
      } else {
        // changeView(lowBpmReadings[randomIndex]);
        // changeView({ Systolic: 180, Diastolic: 95, "Heart Rate": 80 })
      }
      changeThreshold(bpmUpperThreshold);
    } else if (device === "Glucose Meter") {
      if (Math.random() > 0.5) {
        triggerAlert(true);
        changeView(highGlucoseReadings[randomIndex]);
      } else {
        changeView(lowGlucoseReadings[randomIndex]);
        // changeThreshold(glucoseLowerThreshold)
      }
      changeThreshold(glucoseUpperThreshold);
    }
  }, [props.device, dataCaptured]);

  let dataToHold = [];
  Object.entries(dataCaptured).forEach((dataSet) => {
    dataToHold.push(dataSet);
  });

  return (
    <div className="container-fluid text-center font-weight-bold">
      <div className="BrandingContainer mb-2 mt-3">
        <img
          className="img-fluid"
          src={Theme.companyLoginImage}
          alt="Health Logo"
          style={{ width: "230px", height: "200px", marginBottom: '10vw' }}
        />
      </div>

      <div className="PageContainer mb-5">
        <h4 className="m-1">Hello John!</h4>
        <h4 id="CurrentReadingHeader" className="m-2 pl-0 pr-0 p-1">
          Your current readings:
        </h4>
      </div>

      {dataToHold.map((dataSet) => {
        return (
          <div key={dataSet[0]} className="row mb-4">
            <div className="col-2"></div>
            <h5 className="col p-2 mb-1">
              {/* <div > */}

                {/* Metric field name */}
                {dataSet[0] + ": "} 
                {/* Metric field value */}
                {(readingLimit[dataSet[0]] <= dataSet[1]) ? (
                  <span style={{color: "#f55e67" }}>
                    {dataSet[1]}
                  </span>
                ) : (
                  
                <span >
                  {dataSet[1]}
                </span>
                  )}
              {/* </div> */}
            </h5>
            <div className="col-2"></div>
          </div>
        );
      })}

      {showAlert ? (
        <div className="row mb-5">
          <div className="col-2"></div>
          <h5 className="col p-2 mb-1 text-danger">
            You are above your limits. An Alert has been sent to your Health
            Care Provider.
          </h5>
          <div className="col-2"></div>
        </div>
      ) : (
        <div className="row mb-5">
          <div className="col-2"></div>
          <h5 className="col p-2 mb-1">You are within range!</h5>
          <div className="col-2"></div>
        </div>
      )}

      
    </div>
  );
}

export default ReadingDetails;



