import React from "react";
import RegularPatientGraph from "./patientData/RegularPatientGraph";

import UserImage from "../../resources/user.png";
import Theme from "../../../Theme/Theme";

class PatientDetails extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      lastReading: { Systolic: 180, Diastolic: 95, "Heart rate": 80 },
      readingLimit: { Systolic: 180, Diastolic: 120, "Heart Rate": 180 },
      currentPatient: props.patient,
    };
  }

  componentDidMount() {
    console.log(this.state.currentPatient.patient.firstName);
  }

  render() {
    return (
      <div className="container-fluid text-center font-weight-bold">
        <div className="BrandingContainer mb-2 mt-5">
          <img
            className="img-fluid rounded-circle"
            src={UserImage}
            alt="Health Logo"
            style={{ width: "150px", height: "150px" }}
          />
        </div>

        <div className="PageContainer mb-4">
          <h4 className="m-1">
            <b>
            {this.state.currentPatient.patient.firstName}{" "}
            {this.state.currentPatient.patient.lastName}
            </b>
          </h4>
          <h5
            id="CurrentReadingHeader"
            className="m-1 pl-0 pr-0 p-1"
            style={{ color: Theme.companySecondaryTextColor }}
          >
            Current readings:
          </h5>
        </div>

        {Object.entries(this.state.lastReading).map((dataSet) => {
          return (
            <div key={dataSet[0]} className="row">
              <div className="col-2"></div>
              <h5 className="col ">
                {/* Metric field name */}
                {dataSet[0] + ": "}
                {/* Metric field value */}
                {this.state.readingLimit[dataSet[0]] <= dataSet[1] ? (
                  <span style={{ color: "#f55e67" }}>{dataSet[1]}</span>
                ) : (
                  <span>{dataSet[1]}</span>
                )}
              </h5>
              <div className="col-2"></div>
            </div>
          );
        })}

        <div className="row mt-3 mb-0">
          <div className="col-2"></div>
          <h5 className="col p-2 mb-0">
            {/* {currentTime.getHours() + ":" + currentTime.getMinutes() + "PM"} */}
            4:40PM
          </h5>
          <div className="col-2"></div>
        </div>

        <div className="row mb-5 mt-0">
          <div className="col-2"></div>
          <h5 className="col">
            {/* {(currentTime.getMonth().toString())+ " " + (currentTime.getDate()+1) + ":" + currentTime.getMinutes() + "PM"} */}
            June 15, 2020
          </h5>
          <div className="col-2"></div>
        </div>

        <div className="row mb-1">
          <div className="col-2"></div>
          <h5
            className="col"
            style={{ color: Theme.companySecondaryTextColor }}
          >
            Blood Pressure History
          </h5>
          <div className="col-2"></div>
        </div>

        <div className="row mb-4 mt-0 ">
          <div className="col-1 col-lg-4"></div>
          <div className="col p-0 m-0">
            <h6 className="text-muted">
              <br />
            </h6>
            <RegularPatientGraph alerts={0} />
          </div>
          <div className="col p-0 m-0">
            <h6 className="text-muted">Alerts</h6>
            <RegularPatientGraph alerts={1} />
          </div>
          <div className="col-1 col-lg-4"></div>
        </div>

        <div>
          <div
            className="btn m-2"
            onClick={this.props.toggleAlertView}
            style={{backgroundColor: Theme.companyPrimaryBGColor, color: Theme.whiteText}}
          >
            back to home
          </div>
          {this.props.isUrgent ? (
            <div
              className="btn"
              onClick={this.props.toggleCalendar}
              style={{backgroundColor: Theme.companyGreenBGColor,color: Theme.whiteText}}
            >
              Contact Patient
            </div>
          ) : (
            <div
              className="btn"
              onClick={this.props.toggleCalendar}
              style={{backgroundColor: Theme.companyGreenBGColor,color: Theme.whiteText}}
            >
              Schedule Consult
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default PatientDetails;
