import React from "react";
// import * as V from 'victory';
import {
  VictoryBar,
  VictoryChart,
  VictoryAxis,
  VictoryTheme,
  VictoryStack,
  VictoryTooltip,
  VictoryVoronoiContainer,
} from "victory";

const nonAlertData = [
  { date: "5/12", systolic: 150, diastolic: 100 },
  { date: "5/13", systolic: 150, diastolic: 100 },
  { date: "5/14", systolic: 130, diastolic: 80 },
  { date: "5/15", systolic: 130, diastolic: 80 },
  { date: "5/20", systolic: 155, diastolic: 110 },
  { date: "5/17", systolic: 170, diastolic: 110 },
  { date: "5/25", systolic: 110, diastolic: 65 },
];

const alertData = [
  { date: "5/16", systolic: 183, diastolic: 135 },
  { date: "5/18", systolic: 190, diastolic: 140 },
  { date: "5/19", systolic: 185, diastolic: 120 },
  { date: "5/21", systolic: 190, diastolic: 120 },
  { date: "5/22", systolic: 185, diastolic: 110 },
  { date: "5/23", systolic: 170, diastolic: 110 },
  { date: "5/24", systolic: 180, diastolic: 125 },
];

function RegularPatientGraph(props) {
  return (
    <VictoryChart
      domainPadding={20}
      theme={VictoryTheme.material}
      padding={{ top: 5, bottom: 50, left: 5, right: 5 }}
      containerComponent={<VictoryVoronoiContainer />}
    >
      <VictoryAxis />
      <VictoryStack
        colorScale={props.alerts === 1 ? "warm" : "qualitative"}
        labelComponent={
          <VictoryTooltip
            // pointerWidth={200}
            // pointerHeight={50}
            style={{ fontSize: 25, fontStyle: "san-serif" }}
            flyoutStyle={{
              stroke: props.alerts === 1 ? "red" : "green",
              strokeWidth: 2,
            }}
          />
        }
      >
        <VictoryBar
          data={props.alerts === 1 ? alertData : nonAlertData}
          x="date"
          y="systolic"
          labels={({ datum }) =>
            `${datum.date} \n Systolic: ${datum.systolic} \n Diastolic: ${datum.diastolic}`
          }
          // labels={() => null}
        />
        <VictoryBar
          data={props.alerts === 1 ? alertData : nonAlertData}
          x="date"
          y="diastolic"
          // labels={({ datum }) => datum.reading}
          labels={({ datum }) =>
            `${datum.date} \n Systolic: ${datum.systolic} \n Diastolic: ${datum.diastolic}`
          }
        />
      </VictoryStack>
    </VictoryChart>
  );
}

export default RegularPatientGraph;
