import React from "react";
import "./App.css";

import PatientHomePage from "./Patient/views/PatientHomePage";

import PraticonerHomePage from "./Praticoner/views/PraticonerHomePage";


import { HashRouter, Route } from "react-router-dom";
import Theme from "./Theme/Theme";

function App() {
  return (
    <div className="App" style={{ fontFamily:Theme.fontFamily}}>
      <HashRouter basename="/">
        <Route path="/patient">
          <PatientHomePage />
        </Route>

        <Route path="/provider">
          <PraticonerHomePage />
        </Route>
      </HashRouter>
    </div>
  );
}

export default App;
